import React, { useRef } from 'react'
import { Container, Form, Button} from 'react-bootstrap'
import '../App.css';


const Login = () => {
  const id = useRef();
  const password = useRef();

  function clipw() {
   const idIv = id.current.value;
   const passIv = password.current.value;
   const emailEn = /^([\w-]+(?:\.[\w-]+)*)@((?:[w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
   const emailRed= /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/;
    const hangulcheck = /[-|-|-]/;

    if (!emailEn.test(idIv)){
      alert('   ');
      
      return false
    } else{
    if (false === emailRed.test(passIv)) {
      alert(' 8  , ///   .');
      password.current.focus();
      return false
    } else if (/(\w)\1\1\1/.test(passIv)) {
     alert('  4    .');
     password.current.focus();
      return false;
    } else if (passIv.search(idIv) > -1) {
      alert("  .");
      password.current.focus();
      return false;
    } else if (passIv.search(/\s/) != -1) {
      alert("   .");
      password.current.focus();
      return false;
    } else if (hangulcheck.test(passIv)) {
     alert("     .");
     password.current.focus();
     return false;
    } else {
      alert(".");
    }

  }
}

  const loginP = {
    width: "100vw" ,
    height: "100vh"
  }

 

  return (
    <Container style={loginP}>
      <h1 style={{marginTop:"80px"}}></h1>
      
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" placeholder="Enter email" id="id" ref={id}/>
        <Form.Text className="text-muted">
          We'll never share your email with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control type="password" placeholder="Password" id="password"  ref={password}/>
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicCheckbox">
        <Form.Check type="checkbox" label="Check me out" />
      </Form.Group>
      <Button variant="primary" onClick={clipw}>
        Submit
      </Button>
   
    </Container>
  )
}

export default Login