import React, { useState } from 'react'
import { Container, Row, Button, Card, Carousel } from 'react-bootstrap'
import Product from '../components/Product';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import '/gentle/src/styles.css';
// import required modules
import { Pagination } from 'swiper/modules';
import Title from '../components/Title';
import Mejong from '../components/Mejong';
import data2 from '../data2';
import axios from 'axios';
import Cardslide from '../components/Cardslide';
import { Scrollbar } from 'swiper/modules';
import 'swiper/css/scrollbar';



const Home = (props) => {

  let { glass, setGlass, no, setNo } = props;
  let [mejong, setMejong] = useState(data2);
  const [count, setCount] = useState(1)
  const title = [{tit_h3:'    ',tit_p:'   \n     \n     '},{tit_h3:'   ',tit_p:'   \n    '},{tit_h3:'2024 COLLECTION',tit_p:'2024  GENTLE JELLY   \n      .\n     .'},{tit_h3:'24 OPTICAL COLLECTION',tit_p:'         \n 2024   . 2024    \n      .'},{tit_h3:' ',tit_p:'    '}]
const [index, setIndex] = useState(0);
  
    const handleSelect = (selectedIndex) => {
      setIndex(selectedIndex);
    };
  // function ControlledCarousel() {
    
    
  // }

 
  return (

    <>

      
      <div className='onevideo'>
        <video autoPlay muted loop playsinline src={process.env.PUBLIC_URL+"/mov/templeless-video-pc.mp4"} className='vvd'></video>
      </div>

      <Title title={title[0]} />
      <div className='bbt1'>
      <Button className='bt1' variant='outline-primary' onClick={() => {
        let copy = [...glass].sort((a, b) => (a.title > b.title) ? 1 : -1)
        setGlass(copy);
        let no_copy = [];
        for (let i in copy) {
          no_copy.push(copy[i].id);
        }
        setNo(no_copy);
      }} > </Button>{' '}
      <Button className='bt2' variant='outline-secondary' onClick={() => {
        let copy = [...glass].sort((a, b) => (a.price > b.price) ? 1 : -1);
        setGlass(copy);
        let no_copy = [];
        for (let i in copy) {
          no_copy.push(copy[i].id);
        }
        setNo(no_copy);
      }}> </Button>{' '}
      <Button className='bt3' variant='outline-success' onClick={() => {
        let copy = [...glass].sort((a, b) => (a.price < b.price) ? 1 : -1);
        setGlass(copy);
        let no_copy = [];
        for (let i in copy) {
          no_copy.push(copy[i].id)
        }
        setNo(no_copy);
      }}> </Button></div>
      <Container>
        <Row>
          {
            glass.map((glas, i) =>
              <Product glass={glas} key={i} i={i} no={no} />
            )
          }
        </Row>
        <Container>
        <Swiper
          direction={'vertical'}
          pagination={{
            clickable: true,
          }}
          modules={[Pagination]}
          className="mySwiper"
        >
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item01-pc.avif"} alt="1" />
            <p className='ttxx'>
                     <br />
                   .
            </p>
          </SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item02-pc.avif"} alt="1" /></SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item04-pc.avif" }alt="1" /></SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item05-pc.avif" }alt="1" /></SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item06-pc.avif"} alt="1" /></SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item07-pc.avif"} alt="1" /></SwiperSlide>
          <SwiperSlide>
            <img src={process.env.PUBLIC_URL+"/img/me_slide/intro-campaign-item08-pc.avif"} alt="1" /></SwiperSlide>
        </Swiper>
        </Container>

        <Title title={title[1]}/>
        <div className='bbt2'>
      <Button className='bt4' variant="outline-dark" onClick={()=>{
        if(count ===1){
          axios.get('https://nayeye13.github.io/react_data_GM/mejong2.json')
          .then((result)=>{
            
            let copy = [...mejong, ...result.data]
            setMejong(copy)
            setCount(2)
          })
        } else if(count ===2){
          axios.get('https://nayeye13.github.io/react_data_GM/mejong3.json')
          .then((result)=>{
            
            let copy = [...mejong, ...result.data]
            setMejong(copy)
            setCount(3)
        })
      } else if(count ===3){
        axios.get('https://nayeye13.github.io/react_data_GM/mejong4.json')
        .then((result)=>{
          
          let copy = [...mejong, ...result.data]
          setMejong(copy)
          setCount(4)
      })
      } else {
        alert('   .')
      }
      }}>+3  </Button>
      </div>
       {' '}
        <Container style={{ marginTop: '30px' }}>
          <Row>
            {
              mejong.map((item, i) =>
                <Mejong mejong={item} key={i} />
              )
            }
          </Row>
        </Container>
        </Container>

        <Title title={title[2]} /> 
        <div className='new'>
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_pc_1-1.avif"} alt="new" /> 
        </div>
         <div className='pic'>
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_product_cut_1-1.avif" }alt="new" />
          <div className='pic2'>
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_product_cut_2.avif"} alt="new" />
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_product_cut_3.avif"} alt="new" />
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_product_cut_4.avif"} alt="new" />
          <img src={process.env.PUBLIC_URL+"/img/2024/campaign_product_cut_5.avif"} alt="new" />
          </div>
        </div>
        <Title title={title[3]} /> 
        <div className='twovideo'>
        <video autoPlay muted loop playsinline src={process.env.PUBLIC_URL+"/mov/optical-campaign-video-1.mp4" }className='vd'></video>
         
        </div>
        
       <div>
        <p className='otx'>VIEW THE COLLECTION</p>
       </div>
        <Swiper
        scrollbar={{
          hide: true,
        }}
        modules={[Scrollbar]}
        className="mySwiper2"
        slidesPerView={3}
        spaceBetween={40}
        loop={true}
      >
      <SwiperSlide>
        <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-1.avif'} alt="ot" />
        <p className="ott1">Ytt 02    </p>
        </SwiperSlide>    
        <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-2.avif'} alt="ot" />
      <p className="ott2">Paradoxx R4   </p>
      </SwiperSlide>
      <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-3.avif'} alt="ot" />
      <p className="ott3">Rrr 02    </p>
        </SwiperSlide>
        <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-4.avif'} alt="ot" />
      <p className="ott4">Third Kind Y8    </p>
      </SwiperSlide>
      <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-5.avif'} alt="ot" />
      <p className="ott5">Kro 02    </p>
      </SwiperSlide>
      <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-6.avif'} alt="ot" />
      <p className="ott6">Atomic 02(B)   </p>
      </SwiperSlide>
      <SwiperSlide>
      <img src={process.env.PUBLIC_URL+'/img/opti/optical-product-7.avif'} alt="ot" />
      <p className="ott7">Vonzo 01    </p>
      </SwiperSlide>
</Swiper>
<div style={{textAlign:"center", margin:"20px",marginTop:"50px"}}>
          <Button style={{padding:"20px 50px",borderRadius:"30px"}} variant="dark"> </Button>{' '}
          <Button style={{padding:"20px  50px",borderRadius:"30px"}} variant="dark"></Button>
          </div>
    <Container style={{marginTop:"60px"}}>
        <Title title={title[4]} /> 
        <Container className='card_wrap'>
        <Cardslide/>
      </Container>
</Container>
    </>

  )

}



export default Home
